import React, { useState } from "react";
import {
  Box,
  Button,
  ButtonGroup,
  CircularProgress,
  Typography
} from "@material-ui/core";
import { getAuth, sendEmailVerification, signOut } from "firebase/auth";
import { Check, HourglassEmpty } from "@material-ui/icons";

export default function LoggedIn({ user }) {
  const [sending, setSending] = useState(false);

  function SendEmailVerification() {
    if (!user) return;

    setSending(true);
    user
      .reload()
      .then(
        () => user != null && !user.emailVerified && sendEmailVerification(user)
      )
      .catch((error) => alert(error.code + ": " + error.messsage))
      .finally(() => setSending(false));
  }

  return (
    <>
      <Typography>Hi {user?.displayName ?? "<<name>>"}</Typography>
      <Typography color="textSecondary">
        {user?.email ?? "<<email>>"}
        {user?.emailVerified ? (
          <Check fontSize="small" color="primary" />
        ) : (
          <HourglassEmpty fontSize="small" color="error" />
        )}
      </Typography>

      <Box height="24px" />

      <ButtonGroup>
        {!user?.emailVerified && (
          <Button onClick={SendEmailVerification}>
            {sending ? <CircularProgress /> : "Resend verification"}
          </Button>
        )}
        <Button color="secondary" onClick={() => signOut(getAuth())}>
          Log out
        </Button>
      </ButtonGroup>
    </>
  );
}
