import * as yup from "yup";

export const nameSchema = yup.string("Enter your name").max(50).required();

export const emailSchema = yup.string("Enter your email").email().required();

export const passwordSchema = yup
  .string("Enter your password")
  .min(8, "Must be at least 8 characters")
  .matches(/^(?=.*[A-Za-z]).*$/, "Must contain a letter")
  .matches(/^(?=.*?[0-9]).*$/, "Must contain a number")
  .matches(/^(?=.*[@$!%*#?&=+-]).*$/, "Must contain a special character")
  .required();

export const confirmPasswordSchema = yup
  .string()
  .test("passwords-match", "Passwords must match", function (value) {
    return this.parent.password === value;
  });
